.features-list {
    // border: 1px solid red;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 2rem;

    &>li{
        border: 1px solid red;
        width: 13rem;
        height: 18rem;
        flex: 1 1 auto;
        margin: 1.5rem;
        // padding: 0 1rem;
        border-radius: 0 0 1rem 1rem;
        padding: 0 1rem;
        transition: .3s;

        &>a{
            // border: 1px solid blue;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 1rem;
            width: 100%;
            height: 100%;

            &>img{
                width: 10rem;
                height: 10rem;
                border-radius: 50%;
                border: 1px solid red;
            }

            &>h3{
                font-size: 1.5rem;
                text-align: center;
                font-weight: 600;
                color: #871550;
            }
        }

        &:hover{
            background-color: #871550;

            &>a {
                &>h3 {
                    color: #fff;
                }
            }
        }
    }
}