.course-cart {
    // border: 1px solid blue;
    background-color: #EEEEEE;
    width: 25rem;
    height: 25rem;
    margin: 1.5rem;
    flex: 1 1 auto;

    &>a {
        // border: 1px solid red;
        width: 100%;
        height: 100%;
        display: flex;
        gap: 2rem;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        


        &>figure {
            width: 12.5rem;
            height: 12.5rem;
            border-radius: 50%;
            border: 1px solid red;

            &>img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &>h3{
            color: #871550;
        }
    }
}